.mainContentBox {
  display: flex;
  flex-flow: row wrap;
  gap: 25px;
  align-items: flex-start;
  justify-content: center;

  margin-top: 30px;
  padding: 0 50px;

  .todolist {
    flex: 1 0 220px;

    .removeIcon {
      position: absolute;
      top: -14px;
      right: -14px;
    
      display: flex;
      align-items: center;
      justify-content: center;
    
      width: 25px;
      height: 25px;
    
      background-color: ghostwhite;
      border-radius: 50%;
    }
    
    .customFocusVisible {
      background-color: transparent !important;
    }
    
    
  }
}

.start {
  justify-content: start;
}
