.taskBox {
  position: relative;

  display: flex;
  align-items: center;
  justify-content: space-between;

  width: 100%;
  margin-top: 15px;

  border-bottom: 0.3px solid lightslategray;

  & .title {
    width: 100%;
    font-size: 20px;
    text-align: center;
    overflow-wrap: anywhere;
  }
}
